import { put, call, select, takeEvery } from 'redux-saga/effects';

import api from '../api';
import { orders as actions } from '../actions';
import { orders as selectors } from '../selectors';

// saga for fetching all orders from the API
export function* ordersGetAll() {
  try {
    const isFetching = yield select(selectors.getIsFetching);

    if (!isFetching) {
      yield put(actions.ordersFetchStart());

      const response = yield call(api, 'orders');

      yield put(actions.ordersFetchSucceeded(response));
    }
  } catch (error) {
    yield put(actions.ordersFetchFailed(error));
  }
}

// saga for fetching all orders from the API if state is empty or invalidated
export function* ordersGetAllIfNeeded() {
  try {
    const shouldFetch = yield select(selectors.shouldFetch);

    if (shouldFetch) {
      yield put(actions.ordersFetchStart());

      const response = yield call(api, 'orders');

      yield put(actions.ordersFetchSucceeded(response));
    }
  } catch (error) {
    yield put(actions.ordersFetchFailed(error));
  }
}

// watcher saga for orders
export default function* ordersSaga() {
  yield takeEvery(actions.ORDERS_GET_ORDERS, ordersGetAll);
  yield takeEvery(actions.ORDERS_GET_ORDERS_IF_NEEDED, ordersGetAllIfNeeded);
}



