// @flow

const API_URL =
  process.env.NODE_ENV === 'production'
    ? window.env.API_URL
    : 'http://localhost:4000';
const HEADERS = {
  Accept: 'application/json',
  'Content-Type': 'application/json'
};

const config = {
  API_URL,
  HEADERS
};

export default config;



