// @flow

// import type { OrderType } from '@fixit/fixit-types';
import type { StateType } from '../reducers/orders';

type MainStateType = {
  orders: StateType
};

// isFetching flag selector
export const getIsFetching = (state: MainStateType): boolean =>
  state.orders.isFetching;

// orders object selector
export const getOrdersAsArray = (state: MainStateType): boolean =>
  state.orders.byId.map(id => state.orders.items[id]);

// selector deciding if orders should be fetched
export const shouldFetch = (state: MainStateType): boolean => {
  if (!state.orders.byId.length) {
    return true;
  } else if (state.orders.isFetching) {
    return false;
  }
  return state.orders.didInvalidate;
};

const selectors = {
  getIsFetching,
  getOrdersAsArray,
  shouldFetch
};

export default selectors;



