// @flow

import * as React from 'react';
import { MUIWrapper, ReduxWrapper, RouterWrapper } from './';

type Props = {
  children: React.Node
};

const AppWrappers = ({ children }: Props) => (
  <ReduxWrapper>
    <MUIWrapper>
      <RouterWrapper>
        {children}
      </RouterWrapper>
    </MUIWrapper>
  </ReduxWrapper>
);

export default AppWrappers;



