// @flow

import * as React from 'react';
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';

/**
 * New Colors
 * Yellow: #ffda00
 * Blue: #2071b5
 *
 * Material Design mapping
 * Blue:
 * light - 609fe8
 * medium - 2071b5
 * dark - 004685
 *
 * Yellow:
 * light - ffff53
 * medium - ffda00
 * dark - c7a900
 */

const theme = createMuiTheme({
  palette: {
    primary: {
      light: '#609fe8',
      main: '#2071b5',
      dark: '#004685'
    },
    secondary: {
      light: '#ffff53',
      main: '#ffda00',
      dark: '#c7a900'
    }
  }
});

type Props = {
  children: React.Node
};

const MUIWrapper = ({ children }: Props) => (
  <MuiThemeProvider theme={theme}>
    <div>
      <CssBaseline />

      {children}
    </div>
  </MuiThemeProvider>
);

export default MUIWrapper;



